body {
  margin: 0;
  padding: 0;
  font-family: "Courie New", Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: linear-gradient(90deg, rgba(185,232,78,1) 0%, rgb(147, 72, 218) 0%, rgb(104, 23, 180) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.center{
  display:flex;
  justify-content:center;
}

.App {
  text-align: center;
}

.particle{
  position:fixed;
  top:0;
  bottom:0;
  left:0;
  right:0;
  z-index:-1;
}

.center{
  display:flex;
  justify-content:center;
}


.titl{
    position:absolute;
    width:30%;
    
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;

    color:rgb(252, 228, 10);
    
/* margin-bottom: 50px; */
}

h1{
    text-shadow: -3px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}
.sig{
    background:rgb(106, 13, 160);
    border-radius:20px;
    margin:5px;
    margin-top:15px;
    border:2px solid black;
    /* width:10%; */
}

.sig:hover{
    background:white;
    color:rgb(106, 13, 160);
}
.Tilt{
    background: linear-gradient(90deg, rgb(255, 199, 46) 0%, rgb(247, 223, 15) 100%);

    position:absolute;
    top:10px;
    left:10px;
    border:5px solid black;

}


.rankno span{
    border:3px solid black;
    background: #F9DA16;
    color:black;
    border-radius:50px;
    padding:15px;
}

.rankno span:hover{
    /* background:linear-gradient(90deg,black, rgb(97, 97, 93)); */
    background: black;
    border:3px solid #F9DA16;
    color:#F9DA16;
     

}


.form{
    width:700px;
    background:
radial-gradient(rgba(0, 0, 0, 0.26) 15%, transparent 16%) 0 0,
radial-gradient(rgba(0, 0, 0, 0.384) 15%, transparent 16%) 8px 8px,
radial-gradient(rgba(255,255,255,.1) 15%, transparent 20%) 0 1px,
radial-gradient(rgba(255, 255, 255, 0.1) 15%, transparent 20%) 8px 9px;
background-color:#151618;
background-size:16px 16px;
}

.hovr{
    height:46px;
    border:2px solid black;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    outline:none;
}

input[type=text]{
    text-indent: 12px;
    font-weight: 600;
}

.ima{
    border:none;
    height:46px;
    border:2px solid black;
    border-left:0px;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;

}


.ima:hover{
    background:#F9DA16;
    color:black;
    border:2px solid black;
    cursor:pointer;
    /* border-radius:15px; */

}
.imagesearch{
    margin-top:10px;
    width:500px;
    height:auto;

}
.center{
    display:flex;
    justify-content: center;
}
.bounding-box{
    
    position: absolute;
    box-shadow: inset 0 0 0 5px #f2e3149f;
    border:2px solid black;

    border-radius:10px;

    display: flex;

    flex-wrap: wrap;

    justify-content: center;
    cursor: pointer;
    
}


